<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      tabsNum: 1,
      F5data: {
        allBonus: 0,
        balanceBonus: 0,
        vipLength: 0,
      },
      //   currency: "",
      //   link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      //   timer: null,
      //   timer2: null,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
         protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(34, 42)
        : addr;
    },
    //领取分红奖励
    getvipDividendFun: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      // if (vipAvgBonus <= 0) {
      //   this.$toast(this.$t("message.bonusMsg"));
      //   return;
      // }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .getBonusReward()
        .send({ from: account ,value: '5000000000000000'})
        .then((res) => {
          console.log("getvipDividendFun", res);
          // if (res.status) {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          // _this.getCoinBalance();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          // this.getMiningInfoFun(1);
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  padding-bottom: 1px;
  .van-count-down {
    color: #fff;
  }
background: linear-gradient(104deg,#0f1d28,#03030D,#03030D);
padding-top:60px;

  .title {
    font-size: 26px;
    font-family: SourceHanSansCN-Bold, SourceHanSansCN;
    font-weight: bold;
    color: #4da9db;
    line-height: 39px;
    margin: 0 0 15px;

    span {
      color: #ffa0ae !important;
      font-size: 26px !important;
      font-weight: bold !important;
    }
  }
  .line_box {
    width:98%;
    margin: 30px auto 20px;
    background: url(../../assets/img/fan/min_bg.png) top center no-repeat;
    background-size: 100% 100%;
    position: relative;
    padding: 50px;
    box-sizing: border-box;

    span {
      position: absolute;
         right:20px;
    top: 20px;
    }

    .bt {
      font-size: 12px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #FF7021;
      margin-bottom: 10px;
    }

    h2 {
      font-size: 30px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: bold;
      color: #fff;
    }

    h4 {
      font-size: 14px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #fff;
    }
  }

  .cent {
    width: 92%;
    margin: 30px auto;
    padding: 20px 0 50px;
    position: relative;
    background: #171111;
    border-radius: 15px;
  }

}
  .btn{
    width: 50%;
    margin: 0 auto 20px;
    display: block;
     background: url(../../assets/img/fan/an.png)center no-repeat;
      background-size:100% 100%;
      border: none;
      text-align: center;
      color:#fff;
      font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    font-size: 17px;
    border-radius: 30px;
    padding:20px 0;
  }

    .my {
           background: url(../../assets/img/fan/1.png) top center no-repeat;
      background-size:100% ;
      margin: 0 auto 20px;
      padding: 12px 25px 0;

      .box {
        background: #532E25;
        border-radius: 10px;
        margin: 5px 0;
        padding: 15px 10px;
        text-align: center;

        span {
          color: #fff;

          img {
            margin-right: 5px;
            vertical-align: middle;
          }
        }

        h3 {
          text-align: center;
          color: #FFC38B;
          margin: 8px 0 0 0;
        }
      }
    }

.list {
  // background: #fff;
  border-radius: 0 0 15px 15px;
  // padding: 20px;
    width: 90%;
  margin: 20px auto 20px;
  box-sizing: border-box;
  .line{
    background: #0B0706;
    border-radius: 10px;
    margin:10px 0;
    padding: 5px 10px;
  }
  span {
    display: inline-block;
    padding: 6px 0;
    color: #9F6648;
  }

  .textRight span {
    font-size: 15px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 600;
    color: #fff;
  }
}
</style>



